.cards_container {
  grid-column: 12 / 16;
  grid-row: 3 / 12;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card {
  display: flex;
  justify-content: space-around;
  padding: 1.5rem;
  width: 70%;
  border-radius: 5px;
  background-color: #fbfbfb;
  &_valueContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    flex-grow: 1;
  }
  &_value {
    margin: 0;
    font-size: 20px;
    &_type {
      margin: 0;
      font-size: 14px;
      color: #74798c;
    }
  }
}
